import { UrlHelper } from "../../common/urlHelper";
import { inject, injectable } from "../../domain/core/diContainer";
import { IAutoLogin, IAutoLoginData } from "../../domain/useCase/IAutoLogin";
import { IViewModel } from "../../domain/viewModel/IViewModel";

@injectable()
export class AutoLogin implements IAutoLogin {
    constructor(
        @inject(IViewModel) private readonly viewModel: IViewModel,
    ) { }
    public gatherAutoLoginData() {
        return UrlHelper.gatherQueryString<IAutoLoginData>();
    }
    public trySetDomain(autologinData: IAutoLoginData) {
        if (!autologinData.domain) { return; }

        const autoDomain = this.viewModel.user?.domains.find(domain => domain.name == autologinData.domain);
        if (autoDomain) {
            this.viewModel.user!.selectedDomain = autoDomain;
        }
    }
    public allSystemsGo(autoLoginData: IAutoLoginData) {
        return autoLoginData.domain && autoLoginData.email && autoLoginData.pass ? true : false;
    }
}