import { injectable } from "../../domain/core/diContainer";
import yaml from "js-yaml";
import { IProcessModel, IProcessEditorState, IModelsOfModule } from "../../domain/presentation/editors/IProcessEditorModel";
import { BASE_CONFIG_FILE, EMPTY_CONFIG_FILE, processTypes } from "../../domain/objects/editors/processEditor/constants/processTemplates";
import { IProcessEditorUtil } from "../../domain/useCase/IProcessEditorUtil";
import { IModel, IModule } from "../../domain/model/models";

@injectable()
export class ProcessEditorUtil implements IProcessEditorUtil {
    constructor(private readonly pathPrefix = "/microui/modul_name/qjson/", private readonly screenFileExtension = ".qjson") {}

    public getEmptyProcessWizardModel(moduleName: string) {
        const processModel: IProcessModel = {
            processType: "",
            createNewBpmnFile: true,
            selectedExistingBpmnFile: {
                ID: "",
                variables: [],
            },
            processMakerDefinition: {
                makerScreenID: "",
                useHierarchicalProcess: false,
                instUnitHierarchy: "SAME",
                ignoreLevelForDiffInstUnit: false,
            },
            processCheckerDefinition: {
                firstApproverScreenID: "",
                secondApproverScreenID: "",
                thirdApproverScreenID: "",
                fourthApproverScreenID: "",
                fifthApproverScreenID: "",
            },
            runAfterApprovalDefinition: {
                isRunAfterApprovalEnable: false,
                runAfterApprovalApi: `http://${moduleName}/api/runAfterApprovalApi`,
                runAfterApprovalMethod: "POST",
            },
            runAfterRejectedDefinition: {
                isRunAfterRejectedEnable: false,
                runAfterRejectedApi: `http://${moduleName}/api/runAfterRejectedApi`,
                runAfterRejectedMethod: "POST",
            },
            runAfterChangeAdvisedDefinition: {
                isRunAfterChangeAdvisedEnable: false,
                runAfterChangeAdvisedApi: `http://${moduleName}/api/runAfterChangeAdvisedApi`,
                runAfterChangeAdvisedMethod: "POST",
            },
            reviewScreenID: "",
        };
        return processModel;
    }

    public getEmptyProcessEditorState() {
        return {
            stepper: {
                currentStep: 0,
                totalStep: 0,
                isTabValid: false,
                isApproveTab: false,
            },
            existingBpmnVariableCache: {},
            creatingProcessFileName: "",
        } as IProcessEditorState;
    }

    public generateBpmnFile(processModel: IProcessModel, processName: string) {
        const processFile = processTypes.find((type) => type.value === processModel.processType)?.bpmnFile;
        return processFile.replace(/PROCESS_ID_/gm, processName).replace(/PROCESS_NAME_/gm, processName);
    }

    public generateProcessConfigFile(processModel: IProcessModel, modelsOfModule: IModelsOfModule) {
        if (processModel.createNewBpmnFile) {
            const processFileName = this.getModelNameByID(processModel.selectedExistingBpmnFile.ID, modelsOfModule.bpmns);
            const configFile: any = yaml.load(BASE_CONFIG_FILE);
            const approvalConfigFile: any = yaml.load(processTypes.find((type) => type.value === processModel.processType)?.configFile);
            const approvalConfigFileVariables = approvalConfigFile!.rally!.process["auto-deploy"].metadata.processMetadata[0].variables;

            if (configFile!.rally!.process["auto-deploy"]) {
                const processDefinition = configFile!.rally!.process["auto-deploy"].processes[0];
                const processMetadata = configFile!.rally!.process["auto-deploy"].metadata.processMetadata[0];
                let processVariables = configFile!.rally!.process["auto-deploy"].metadata.processMetadata[0].variables;

                processDefinition.path = processDefinition.path.replace("BPMN___FILE___NAME", processFileName);
                processDefinition.key = processFileName;

                processMetadata.identifierName = processFileName;
                processMetadata.processName = processFileName;
                processMetadata.resourceCode = processFileName;

                // TODO: process_name will be changed when export/deploy is applied.
                processVariables.map(
                    (variable: any) => (variable.value = this.handleProcessVariables(processModel, variable.key, modelsOfModule, "process_name"))
                );
                approvalConfigFileVariables.map(
                    (variable: any) => (variable.value = this.handleProcessVariables(processModel, variable.key, modelsOfModule, "process_name"))
                );
                configFile!.rally!.process["auto-deploy"].metadata.processMetadata[0].variables =
                    processVariables.concat(approvalConfigFileVariables);
                configFile!.rally!.process["auto-deploy"].metadata.makerCheckerRoles = this.getMakerCheckerRoles(
                    processModel,
                    modelsOfModule.bpmns,
                    "process_name"
                );
            }
            console.log(yaml.dump(configFile));
            return yaml.dump(configFile);
        } else {
            const processFileName = this.getModelNameByID(processModel.selectedExistingBpmnFile.ID, modelsOfModule.bpmns);
            const configFile: any = yaml.load(EMPTY_CONFIG_FILE);
            const processDefinition = configFile!.rally!.process["auto-deploy"].processes[0];
            const processMetadata = configFile!.rally!.process["auto-deploy"].metadata.processMetadata[0];
            processDefinition.path = processDefinition.path.replace("BPMN___FILE___NAME", processFileName);
            processDefinition.key = processFileName;

            processMetadata.identifierName = processFileName;
            processMetadata.processName = processFileName;
            processMetadata.resourceCode = processFileName;
            configFile!.rally!.process["auto-deploy"].metadata.processMetadata[0].variables = processModel.selectedExistingBpmnFile.variables.map(
                (variable) => ({
                    key: variable.key,
                    type: variable.type === "BOOLEAN" ? "BOOLEAN" : "STRING",
                    value:
                        variable.type === "BOOLEAN"
                            ? variable.value === "true"
                                ? true
                                : false
                            : variable.type === "SCREEN"
                            ? `${this.pathPrefix}${variable.value}${this.screenFileExtension}`
                            : `${variable.value}`,
                })
            );
            console.log(yaml.dump(configFile));
            return yaml.dump(configFile);
        }
    }

    public extractEditableProcessVariables(processVariables: Array<any>, ignoredVariables: Array<string>) {
        const scopes: any = [];
        const flowElements: any = [];
        const variables: any = [];
        processVariables = processVariables || [];
        processVariables
            .map((variable) => variable.scope)
            .forEach((scope) => {
                if (!scopes.some((scope1: any) => JSON.stringify(scope) === JSON.stringify(scope1))) {
                    scopes.push(scope);
                }
            });

        scopes.forEach((scope: any) =>
            scope.flowElements.forEach((flowElement: any) => {
                if (flowElement.$type === "bpmn:CallActivity" || flowElement.$type === "bpmn:UserTask") {
                    flowElements.push(flowElement);
                }
            })
        );

        flowElements.forEach((flowElement: any) => {
            if (flowElement.$type === "bpmn:CallActivity") {
                flowElement.extensionElements?.values.forEach((value: any) => {
                    if (value.source && value.$type === "camunda:In" && !ignoredVariables.includes(value.source)) {
                        const variable = variables.find((variable: any) => variable.target === value.target && variable.source === value.source);
                        if (variable) {
                            variable.elements.push(flowElement.id);
                        } else {
                            variables.push({ ...value, value: "", elements: [flowElement.id] });
                        }
                    }
                });
            } else if (flowElement.$type === "bpmn:UserTask") {
                variables.push({
                    $type: flowElement.$type,
                    source: flowElement.formKey.replace("$", "").replace("{", "").replace("}", ""),
                    target: flowElement.assignee.replace("$", "").replace("{", "").replace("}", ""),
                    value: "",
                    elements: [flowElement.id],
                });
            }
        });
        return variables;
    }

    public handleProcessVariables(processModel: IProcessModel, key: any, modelsOfModule: IModelsOfModule, processName: string) {
        const processCheckerDefinition = processModel.processCheckerDefinition;
        switch (key) {
            case "resourceCode":
                return this.getModelNameByID(processModel.selectedExistingBpmnFile.ID, modelsOfModule.bpmns);
            case "runAfterApprovalApi":
                return processModel.runAfterApprovalDefinition.runAfterApprovalApi;
            case "runAfterApprovalMethod":
                return processModel.runAfterApprovalDefinition.runAfterApprovalMethod;
            case "isRunAfterApprovalEnable":
                return processModel.runAfterApprovalDefinition.isRunAfterApprovalEnable;
            case "runAfterRejectedApi":
                return processModel.runAfterRejectedDefinition.runAfterRejectedApi;
            case "runAfterRejectedMethod":
                return processModel.runAfterRejectedDefinition.runAfterRejectedMethod;
            case "isRunAfterRejectedEnable":
                return processModel.runAfterRejectedDefinition.isRunAfterRejectedEnable;
            case "runAfterChangeAdvisedApi":
                return processModel.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedApi;
            case "runAfterChangeAdvisedMethod":
                return processModel.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedMethod;
            case "isRunAfterChangeAdvisedEnable":
                return processModel.runAfterChangeAdvisedDefinition.isRunAfterChangeAdvisedEnable;
            case "detailedForm":
                return this.getModelNameByID(processModel.processMakerDefinition.makerScreenID, modelsOfModule.screens);
            case "startForm":
                return (
                    this.pathPrefix +
                    this.getModelNameByID(processModel.processMakerDefinition.makerScreenID, modelsOfModule.screens) +
                    this.screenFileExtension
                );
            case "reviewForm":
                return this.pathPrefix + this.getModelNameByID(processModel.reviewScreenID, modelsOfModule.screens) + this.screenFileExtension;
            case "useHierarchicalProcess":
                return processModel.processMakerDefinition.useHierarchicalProcess;
            case "instUnitHierarchy":
                return processModel.processMakerDefinition.instUnitHierarchy;
            case "ignoreLevelForDiffInstUnit":
                return processModel.processMakerDefinition.ignoreLevelForDiffInstUnit;
            case "supportingForm":
                return (
                    this.pathPrefix +
                    this.getModelNameByID(processCheckerDefinition.firstApproverScreenID, modelsOfModule.screens) +
                    this.screenFileExtension
                );
            case "supportingForm2":
                return (
                    this.pathPrefix +
                    this.getModelNameByID(processCheckerDefinition.secondApproverScreenID, modelsOfModule.screens) +
                    this.screenFileExtension
                );
            case "supportingForm3":
                return (
                    this.pathPrefix +
                    this.getModelNameByID(processCheckerDefinition.thirdApproverScreenID, modelsOfModule.screens) +
                    this.screenFileExtension
                );
            case "supportingForm4":
                return (
                    this.pathPrefix +
                    this.getModelNameByID(processCheckerDefinition.fourthApproverScreenID, modelsOfModule.screens) +
                    this.screenFileExtension
                );
            case "supportingForm5":
                return (
                    this.pathPrefix +
                    this.getModelNameByID(processCheckerDefinition.fifthApproverScreenID, modelsOfModule.screens) +
                    this.screenFileExtension
                );
            case "checkerRoles2":
                return processName + ".secondChecker";
            case "checkerRoles3":
                return processName + ".thirdChecker";
            case "checkerRoles4":
                return processName + ".fourthChecker";
            case "checkerRoles5":
                return processName + ".fifthChecker";
            default:
                return "";
        }
    }

    private getMakerCheckerRoles(processModel: IProcessModel, bpmns: IModel[], processName: string) {
        return [
            {
                processId: this.getModelNameByID(processModel.selectedExistingBpmnFile.ID, bpmns),
                makerRole: processName + ".maker",
                checkerRoles: processName + ".firstChecker",
                domainType: "ALL",
            },
        ];
    }

    public getModelsOfModule(module: IModule) {
        const moduleModels: IModelsOfModule = { bpmns: [], screens: [] };
        // When folder feature is appeared, below lines should be reworked.
        module.children.forEach((children: any) => {
            if (children.objectType == "model") {
                if (children.modelType === "qjson") {
                    moduleModels.screens.push(children);
                } else if (children.modelType === "bpmn") {
                    moduleModels.bpmns.push(children);
                }
            }
        });
        return moduleModels;
    }

    private getModelNameByID(modelID: string, models: IModel[]) {
        return models.find((model) => model.ID == modelID)?.name;
    }
}
