import { defineAsyncComponent, ref } from "vue";
import { Watcher } from "../../common/watcher";
import { container, inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IApplication, IFolder, IModel, IModelBodyObject, IModelModifyInfo, IModule, ITreeviewItem } from "../../domain/model/models";
import { createError } from "../../domain/model/shellError";
import { IAsyncComponentCreator } from "../../domain/presentation/IAsyncComponentCreator";
import { IDialog } from "../../domain/presentation/IDialog";
import { INotification } from "../../domain/presentation/INotification";
import { IActionItem, IMenuItem, ITreeItem, ITreeItemIcon, ITreeView, ITreeViewInstances } from "../../domain/presentation/ITreeView";
import { ICreateModel, ICreateModelParam } from "../../domain/useCase/ICreateModel";
import { IFileExplorer } from "../../domain/useCase/IFileExplorer";
import { IObjectUseCase } from "../../domain/useCase/IObjectUseCase";
import { IStudio } from "../../domain/useCase/IStudio";
import { IUseCaseExecutor } from "../../domain/useCase/IUseCaseExecutor";
import { IViewModel } from "../../domain/viewModel/IViewModel";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class FileExplorer implements IFileExplorer {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(INotification) private readonly notification: INotification,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(IViewModel) private readonly viewModel: IViewModel,
        @inject(IUseCaseExecutor) private readonly executor: IUseCaseExecutor,
        @inject(IStudio) private readonly studio: IStudio,
        @inject(IDialog) private readonly dialog: IDialog,
        @inject(IAsyncComponentCreator) private readonly compCreator: IAsyncComponentCreator,
        @inject(IObjectUseCase) private readonly objectUseCase: IObjectUseCase,
    ) { }

    private _feTreeview?: ITreeView;
    private get feTreeview() {
        if (!this._feTreeview) {
            this._feTreeview = container.resolve<ITreeView>(ITreeViewInstances.FileExplorer);
        }
        return this._feTreeview;
    }

    private convertToTreeItems(items: Array<IModule | IFolder | IModel>): {treeItems: Array<ITreeItem>, settingsItems: Array<ITreeItem>} {
        const settingsItems: Array<ITreeItem> = [];
        const converter = (items: Array<IModule | IFolder | IModel>) => items.map(item => {
            // const delAction: IActionItem = { type: "IActionSingleItem", name: "Del", selectCb: this.executor.wrap(() => this.onItemDelete(item), { loading: true }) };
          
            const treeItem: ITreeItem = { id: item.ID, name: item.name, typeIcon: {icon: ''}, actions: [], };
            switch (item.objectType) {
                case "folder":
                   
                case "module":
                    
                    treeItem.typeIcon.icon = 'mdi mdi-puzzle';
                    treeItem.children = converter(item.children);
                    // treeItem.actions!.unshift({ type: "IActionSingleItem", name: "Add",icon:'mdi mdi-plus', selectCb: this.executor.wrap(() => this.onNewItemSelect(item.ID, item.name, item.objectType), { loading: true }) });
                    treeItem.actions!.push({                                                                                                                         
                        type: "IActionMenuItem", name: "", icon:'mdi mdi-plus', tooltip:'Add', children: () => [
                            // { name: "Folder",icon:"mdi mdi-folder", color:'red', selectCb: this.executor.wrap(() => this.onNewItemSelect(item.ID, item.name, item.objectType, 'Folder'), { loading: true }) },
                            { name: "UI Screen",icon:"mdi mdi-palette", color:'#449DD1', selectCb: this.executor.wrap(() => this.onNewItemSelect(item.ID, item.name, item.objectType, 'Screen'), { loading: true }) },
                            { name: "Entity",icon:"mdi mdi-database", color:'#007C77', selectCb: this.executor.wrap(() => this.onNewItemSelect(item.ID, item.name, item.objectType, 'Entity'), { loading: true }) },
                            { name: "Process Diagram",icon:"mdi mdi-vector-polyline", color:'#007C77', selectCb: this.executor.wrap(() => this.onNewItemSelect(item.ID, item.name, item.objectType, 'Process Diagram'), { loading: true }) },
                            { name: "Process Wizard",icon:"mdi mdi-auto-fix", color:'#007C77', selectCb: this.executor.wrap(() => this.onNewItemSelect(item.ID, item.name, item.objectType, 'Process Wizard'), { loading: true }) }
                        ]
                    });
                    treeItem.actions!.push({
                        type: "IActionMenuItem", name: "", icon: "mdi mdi-dots-horizontal", children: [
                            { name: "Edit", icon: "mdi mdi-pencil", selectCb: this.executor.wrap(() => this.onEditModule(item.name, item.ID), { loading: true }) },
                            { name: "Remove", icon: "mdi mdi-minus-circle-outline", selectCb: this.executor.wrap(() => this.remove(item as IModule), { loading: true }) },
                            { name: "Delete", icon: "mdi mdi-delete-outline", color: 'red', selectCb: this.executor.wrap(() => this.onItemDelete(item), { loading: true }) },
                        ]
                    });
                    return treeItem;
                case "model":
                       
                    if(item.modelType=='qjson'|| item.modelType=='js' ||item.modelType=='yaml' ){
                        treeItem.typeIcon.icon = 'mdi mdi-palette';
                        treeItem.typeIcon.color = '#449DD1';
                    }
                    if(item.modelType=='entityDesigner'){
                        treeItem.typeIcon.icon = 'mdi mdi-database ';
                        treeItem.typeIcon.color = '#E9724C';
                    }
                    if(item.modelType=='bpmn'){
                        treeItem.typeIcon.icon = 'mdi mdi-vector-polyline';
                        treeItem.typeIcon.color = '#007C77';
                    }
                     
                    if(item.modelType=='process'){
                        treeItem.typeIcon.icon = 'mdi mdi-auto-fix';
                        treeItem.typeIcon.color = '#007C77';
                    }

                    const checkoutIcon: ITreeItemIcon = { icon: "mdi mdi-arrow-bottom-right-bold-outline", tooltip:'Check out' };
                    const modifiedIcon: ITreeItemIcon = { icon: "", text: "⚫",fontsize:'6px',margin:'7px',tooltip:'Modified'  };

                    if (item.checkedOut) {
                        treeItem.itemIcons = treeItem.itemIcons || [];
                        treeItem.itemIcons.push(checkoutIcon);
                    }
                    if (item.modified.model) {
                        treeItem.itemIcons = treeItem.itemIcons || [];
                        treeItem.itemIcons.push(modifiedIcon);
                    }
                    new Watcher(item, "checkedOut", { preventSameInstance: true }).onChange(newValue => {
                        const target:keyof ITreeView = newValue ? "addItemIcon" : "removeItemIcon";
                        this.feTreeview[target](item.ID, checkoutIcon);
                    });
                    let modelWatcher:Watcher<IModelModifyInfo, "model"> | undefined;
                    const watchModifiedModel = () => {
                        modelWatcher?.unwatch();
                        modelWatcher = new Watcher(item.modified, "model", { preventSameInstance: true });
                        modelWatcher.onChange(newValue => {
                            const target:keyof ITreeView = newValue ? "addItemIcon" : "removeItemIcon";
                            this.feTreeview[target](item.ID, modifiedIcon);
                        });
                    };
                    watchModifiedModel();
                    new Watcher(item, "modified", { preventSameInstance: true }).onChange((newValue, oldValue) => {
                        watchModifiedModel();
                        if (Boolean(oldValue.model) == Boolean(newValue.model)) { return; }

                        const target:keyof ITreeView = newValue.model ? "addItemIcon" : "removeItemIcon";
                        this.feTreeview[target](item.ID, modifiedIcon);
                    });
                    
                    treeItem.cb = {
                        select: this.executor.wrap(() => this.onItemSelect(item), { loading: true }),
                    }

                    // treeItem.actions!.unshift({ type: "IActionSingleItem", name: "His", selectCb: this.executor.wrap(() => this.onShowHistories(item.name, item.ID), { loading: true }) });
                    treeItem.actions!.unshift({ type: "IActionSingleItem", name: "", icon: "mdi mdi-content-save",tooltip:"Save", selectCb: this.executor.wrap(() => this.studio.save(item), { loading: true }) });
                    treeItem.actions!.push({
                        type: "IActionMenuItem", name: "", icon: 'mdi mdi-dots-horizontal', children: () => {
                            const checkin:IMenuItem = { name: "Check in", icon: "mdi mdi mdi-arrow-top-left-bold-outline", selectCb: this.executor.wrap(async () => this.checkinDailog(item), { loading: true }) };
                            const checkout:IMenuItem = { name: "Check out", icon: "mdi mdi mdi-arrow-bottom-right-bold-outline", selectCb: this.executor.wrap(async () => this.studio.checkout(item), { loading: true }) };
                            const undocheckout:IMenuItem = { name: "Undo Check out", icon: "mdi mdi-arrow-bottom-left", selectCb: this.executor.wrap(async () => this.studio.release(item), { loading: true }) };
                            return [
                                { name: "Save", icon: "mdi mdi-content-save-outline", selectCb: this.executor.wrap(() => this.studio.save(item), { loading: true }) },
                                { name: "History", icon: "mdi mdi-history", selectCb: this.executor.wrap(() => this.onShowHistories(item.name, item.ID), { loading: true }) },    
                                item.checkedOut ? checkin : checkout,
                                item.checkedOut ? undocheckout : undefined,
                                { name: "Delete", icon: "mdi mdi-delete-outline", color: 'red', selectCb: this.executor.wrap(() => this.onItemDelete(item), { loading: true }) },
                            ].filter<IMenuItem>((item): item is IMenuItem => item != undefined);
                        }
                    }); 

                    if (item.usageType == "appSettings") { settingsItems.push(treeItem); return undefined; }
                    return treeItem;
            }
        }).filter<ITreeItem>((item): item is ITreeItem => item != undefined);
        const treeItems = converter(items);

        return { treeItems, settingsItems };
    }

    public addNewItem(item: IModule | IFolder | IModel, parentId?: string) {
        const convertedItems = this.convertToTreeItems([item]);
        this.feTreeview.addItem(convertedItems.treeItems, parentId);
        this.feTreeview.addItem(convertedItems.settingsItems, "002");
    }

    public removeItem(itemID: string) {
        this.feTreeview.removeItem(itemID);
    }

    private createFileExplorerTreeView(rootDiv: HTMLElement) {
        //TODO: Treeview.reset çağrılmalı. instance registration komple değişmeli.
        const feTreeview = container.resolve<ITreeView>(ITreeView);
        (container as any)._registry._registryMap.delete(ITreeViewInstances.FileExplorer);
        feTreeview.setup(rootDiv);
        container.registerInstance<ITreeView>(ITreeViewInstances.FileExplorer, feTreeview);
    }

    private createSettingsTreeItem(settingsItems: Array<ITreeItem>) {
        const settingsTreeItem: ITreeItem = { id: "002", name: "UI Settings", orderIndex: 0, typeIcon: {icon: ""}, children: settingsItems };
        const createSettingsModel = (createObject: ICreateModelParam) => this.executor.execute(async () => {
            let settingsModel = await this.executor.executeUseCase<ICreateModel>(ICreateModel, createObject);
            await this.studio.checkout(settingsModel);
            this.studio.openItem(settingsModel);
        }, { loading: true });
        
        settingsTreeItem.actions = [{
            type: "IActionMenuItem", name: "", icon: 'mdi mdi-plus', children: () => {
                const existingDict = this.viewModel.studio.items.reduce((prev, curItem) => {
                    if (curItem.objectType != "model" || curItem.usageType != "appSettings") { return prev; }

                    prev[curItem.name] = true; return prev;
                }, {} as Record<string, boolean>);
                
                const show = (menuItem: IMenuItem) => existingDict[menuItem.name] ? undefined : menuItem;
                const parentObjectId = this.viewModel.studio.appId;
                return [
                    show({ name: "Alert Component", icon: "mdi mdi-palette", color: '#449DD1', selectCb: () => createSettingsModel({ modelType: "qjson", name: "Alert Component", parentObjectId, usageType: "appSettings", modelAdditionals: { qjsonType: "qjson" }, modelBody: [{ key: "qjson", model: "" }] }) }),
                    show({ name: "Pipeline", icon: "mdi mdi-palette", color: '#449DD1', selectCb: () => createSettingsModel({ modelType: "qjson", name: "Pipeline", parentObjectId, usageType: "appSettings", modelAdditionals: { qjsonType: "qjson" }, modelBody: [{ key: "qjson", model: "" }] }) }),
                    show({ name: "Loading Component", icon: "mdi mdi-palette", color: '#449DD1', selectCb: () => createSettingsModel({ modelType: "qjson", name: "Loading Component", parentObjectId, usageType: "appSettings", modelAdditionals: { qjsonType: "qjson" }, modelBody: [{ key: "qjson", model: "" }] }) }),
                    show({ name: "Settings File", icon: "mdi mdi-cog-outline", color: '#449DD1', selectCb: () => createSettingsModel({ modelType: "yaml", name: "Settings File", parentObjectId, usageType: "appSettings", modelAdditionals: {}, modelBody: [{ key: "yaml", model: "" }] }) }),
                    show({ name: "Container Services File", icon: "mdi mdi-cog-outline", color: '#449DD1', selectCb: () => createSettingsModel({ modelType: "js", name: "Container Services File", parentObjectId, usageType: "appSettings", modelAdditionals: {}, modelBody: [{ key: "js", model: "" }] }) }),
                    show({ name: "Style File", icon: "mdi mdi-cog-outline", color: '#449DD1', selectCb: () => createError({ message: "not implemented", type: "technical" }) }),
                    show({ name: "Component List", icon: "mdi mdi-cog-outline", color: '#449DD1', selectCb: () => createError({ message: "not implemented", type: "technical" }) }),
                ].filter<IMenuItem>((item): item is IMenuItem => item != undefined);
            }
        }];
        return settingsTreeItem;
    }

    public loadFileExplorer(rootDiv: HTMLElement) {
        this.createFileExplorerTreeView(rootDiv);

        
        const frontItems = this.convertToTreeItems(this.viewModel.studio.items);
        const settingsTreeItem = this.createSettingsTreeItem(frontItems.settingsItems);
        frontItems.treeItems.unshift(settingsTreeItem);
        this.feTreeview.addItem(frontItems.treeItems);
        // this.feTreeview.setHeader({
        //     text: this.viewModel.studio.appName, actions: [
        //         { name: "+Add", selectCb: () => this.onNewItemSelect(this.viewModel.studio.appId, "application") },
        //         { name: "Coll", selectCb: () => { this.feTreeview.collapseAll() } },
        //     ], actionsWithRoot: true
        // });
    }

    public async onNewItemSelect(parentId: string, parentName: string, parentType: "folder" | "module" | "application", createType:string) {
        this.dialog.showDialog(
            this.compCreator.createNewItemComponent(),
            { closable: true, title: "Create New"+'    '+createType, closeOnOutClick: true, height: createType=='Module' ?'316pxpx':'186px', width:'420px' },
            { parentId, parentName, parentType,createType }
        );
    }

    public  checkinDailog(item:IModel){
        this.dialog.showDialog(defineAsyncComponent(() => import("../../presentation/vue3/components/dialogs/studio/savePlus.vue")), { closable: true, title:'Check in-'+item.name },{});
    }

    private async remove(module: IModule){
       this.executor.execute(async () => {
        const applicationID = this.viewModel.studio.appId;
        await this.studio.detachModuleFromApplication(module, applicationID);
    }, { loading: true });
    }

    public selectItem(itemID: string) {
        this.feTreeview.selectItem(itemID);
    }

    public unselectItem(itemID: string) {
        this.feTreeview.unselectItem(itemID);
    }

    private async onItemSelect(item: IModel) {
        await this.studio.openItem(item);
    }

    private async onShowHistories(modelName: string, modelID: string) {
        console.log('Parameters :\n' + modelName + '\n' + modelID);
        this.dialog.showDialog(this.compCreator.createModelHistoriesComponent(), { closable: true, title: 'History-'+modelName, closeOnOutClick: true, width:'700px', height:'500px',overflow:'auto' }, { modelID });
    }

    private async onEditModule(moduleName: string, moduleID: string) {
        console.log('Edit : ' + moduleID);
        this.dialog.showDialog(this.compCreator.createEditModuleComponent(), { closable: true, title: moduleName, closeOnOutClick: true }, { moduleID });
    }


    //This method has an usacase:DeleteModelImpl and we may need to delete it!..
    private async onItemDelete(item: IModule | IFolder | IModel) {
        await this.objectUseCase.deleteObject(item.ID);
    }
}